import { onMounted, ref } from 'vue'

export const useScroll = (onScroll = () => {}) => {
  const scrollPosition = ref(null)

  onMounted(() => {
    window.addEventListener('scroll', updateScroll)
  })

  const updateScroll = () => {
    scrollPosition.value = window.scrollY
    onScroll()
  }

  return {
    scrollPosition
  }
}
