<template>
  <header
    class="mobile-header"
    :class="{
      'mobile-header__top': isTop
    }"
  >
    <div class="mobile-header__logo">
      <div class="mobile-header__logo-icon">
        <Icon
          :name="isTop ? 'white-bordered-logo' : 'logo'"
        />
      </div>
      <Icon
        :name="isTop ? 'white-logo-title' : 'logo-title'"
        width="67px"
        height="12"
      />
    </div>
    <div class="mobile-header__menu">
      <Button
        type="white"
        class="mobile-header__menu__toggle"
        @click="onShowMenu"
      >
        <Icon
          :name="isTop ? 'white-stack' : 'stack'"
        />
      </Button>
      <div v-if="isShowMenu" class="mobile-header__menu__body">
        <MobileHeaderMenuBackground />
        <Button
          class="mobile-header__menu__body-close"
          @click="onHideMenu"
        >
          <Icon name="close" />
        </Button>
        <Icon
          name="logo"
          width="55px"
          height="55px"
          class="mobile-header__menu__body-icon"
        />
        <div class="mobile-header__menu__body-links">
          <Button
            v-for="item in routes"
            :key="item.name"
            type="white"
            class="mobile-header__menu__link"
            @click="linkClick(item)"
          >
            <Text weight="700" size="40px" line-height="40px">{{ item.title }}</Text>
          </Button>
          <Button
            class="mobile-header__menu__link"
            type="white"
            @click="onContactSupport"
            @mouseover="toggleShowEmail(true)"
            @mouseleave="toggleShowEmail(false)"
          >
            <Text weight="700" size="40px" line-height="40px">{{ isShowEmail ? contactEmail : 'Contact' }}</Text>
          </Button>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import { computed, ref } from 'vue'
import { useHeader } from '@/components/Header/useHeader'
import Text from '@/components/UIKit/Text/Text'
import Icon from '@/components/UIKit/Icon'
import Button from '@/components/UIKit/Button'
import MobileHeaderMenuBackground from '@/components/Header/MobileHeaderMenuBackground'
import { useScroll } from '@/utils/useScroll'

export default {
  name: 'MobileHeader',
  components: {
    MobileHeaderMenuBackground,
    Icon,
    Text,
    Button
  },
  setup () {
    const {
      routes,
      breakpoints,
      isShowEmail,
      activeRoute,
      contactEmail,
      linkClick: onLinkClick,
      toggleShowEmail,
      onContactSupport: contactSupport
    } = useHeader()
    const { scrollPosition } = useScroll()

    const isShowMenu = ref(false)

    const isTop = computed(() => {
      return scrollPosition.value < 50
    })

    const onShowMenu = () => {
      isShowMenu.value = true
    }
    const onHideMenu = () => {
      isShowMenu.value = false
    }
    const linkClick = item => {
      onLinkClick(item)
      onHideMenu()
    }
    const onContactSupport = item => {
      contactSupport(item)
      onHideMenu()
    }

    return {
      isTop,
      routes,
      isShowMenu,
      breakpoints,
      isShowEmail,
      activeRoute,
      contactEmail,
      linkClick,
      onHideMenu,
      onShowMenu,
      toggleShowEmail,
      onContactSupport
    }
  }
}
</script>

